.form {
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
  border: none;
}

.label {
  display: flex;
  flex-direction: column;
  margin: 0.5rem auto;
  align-items: center;
  font-size: large;
  text-align: left;
  padding: 0.5rem 1rem 0.5rem 1rem;
  border-radius: 8px;
  border: none;
}

.formBtn {
  margin-top: 1rem;
  margin-bottom: 2rem;
  background-color: rgba(90, 135, 227, 0.8);
  border: none;
  border-radius: 6px;
  color: #fff;
  flex-grow: 1;

  font-size: 15px;
  height: 36px;
  line-height: 20px;
  margin-left: 8px;
  margin-right: 6px;
  min-width: 185px;
  padding: 0 16px 0 16px;
  color: white;
  cursor: pointer;
}

.formBtn:hover {
  background-color: rgb(27, 38, 60);
  cursor: pointer;
}
